.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #11131a;
  min-height: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.container {
  width: 40%; /* set width to 100% of the parent container */
  height: 300px; /* set height to the desired height of the cropped image */
  overflow: hidden; /* hide any overflow outside of the container */
  margin: 0 auto;

}

.container img {
  width: 100%; /* set width to 100% of the container */
  height: 100%; /* set height to adjust according to the width */
  object-fit: cover; /* crop the image to fill the container */
  object-position: center; /* center the cropped image */
}

body {  
  background-color: #11131a;
  font-size: calc(10px + 2vmin);
  color: white;
}
nav {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  padding: 1vh;
  margin: 1vh;

}

nav li {
  margin: 2vh;
  font-size: calc(10px + 2vmin);
  
}

.App-link {
  color: #61dafb;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
